@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600;700&display=swap);
nav{width:100%;height:50px;top:0;position:fixed;z-index:99}nav .container-navbar{padding:0px 10px;background:#207dff;max-width:480px;height:inherit;margin:0px auto;display:flex;flex-flow:row nowrap;align-items:center;box-shadow:0px -1px 7px -1px #00000033}nav .container-navbar .container-navbar__title{flex:1 1;text-align:center;color:#fff}nav .container-navbar .container-navbar__title strong{color:#fff}nav .container-navbar .container-navbar__icon-left,nav .container-navbar .container-navbar__icon-right{text-align:center}nav .container-navbar .container-navbar__icon-left a,nav .container-navbar .container-navbar__icon-right a{color:#fff}

.container-landing{max-width:480px;min-height:calc(100% - 50px);margin:0px auto;box-shadow:0px 0px 8px -3px #00000033;padding:10px}.container-landing .card .card-footer-item:hover{cursor:pointer;text-decoration:underline;color:#207dff}.container-landing .empty-message{text-align:center}

.container-create{max-width:480px;min-height:calc(100% - 50px);margin:0px auto;box-shadow:0px 0px 8px -3px #00000033;padding:10px}

:root{--color-primary: #000000;--color-secondary: #dd0000;--color-additional: #ffce00;--color-pure-white: #fff;--color-shadow: #d1d9e6;--color-cool-black: #363636;--color-dope-black: #121313;--color-perfect-wood: rgb(183 160 134);--color-perfect-woodlight: #fff1e2}h1,h2,h3,h4,h5,h6,p{margin:0px !important}*{box-sizing:border-box;margin:0px;padding:0px}*:focus{outline:none}@media (max-width: 480px){html{overflow:hidden}}body{height:100vh;padding:0px;font-family:"Poppins", helvetica}@media (max-width: 480px){body{margin-top:0px;margin-bottom:59px;overflow-x:hidden}}body hr{width:20%;margin:1% auto 1% auto;text-align:center;border:1px solid #121313 !important;border:1px solid var(--color-dope-black) !important;background:#121313;background:var(--color-dope-black)}body .d-flex{display:flex}body .d-none{display:none}#root{height:100%}

